@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
* {
    font-family: 'Montserrat', sans-serif;;
    box-sizing: border-box;
}

html,
body {
 height: 100%;
 position: relative;
 padding: 0;
 margin: 0;
}

a {
    color: #af4c4c !important;
    text-decoration: none;
}

.menu {
    position: absolute;
    color: black;
    right: 50%;
    top: 25%;
    -webkit-transform: translate(50%);
            transform: translate(50%);
    z-index: 10;
    background: black;
    padding: 10px;
    border-radius: 10px;
    width: 30%;
}

.menu-item {
    padding: 5px 10px;
    background: white;
    color: black !important;
    border-radius: 5px;
    text-align: center;
    cursor: pointer;
}

.menuToggle {
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer;
    height: 50px;
    width: 50px;
    background: black;
}

.main-container {
    min-height: 100vh; /* will cover the 100% of viewport */
    overflow: hidden;
    display: block;
    position: relative;
    padding-bottom: 100px; /* height of your footer */
}

.people-container {
    margin: 10px auto;
    width: 75%;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    padding: 10px 0;
}

/* Stuff that's used a bunch */

.center {
    text-align: center;
}

.bold {
    font-weight: 600;
}

.hidden {
    display: none;
}

.hide {
    visibility: hidden;
}

/* Button */

.button {
    margin: 3px;
}
.button button {
    background-color: #65baff;
    border: 1px solid #000000;
    border-radius: 5px;
    cursor: pointer;
    width: 250px;
    padding: 3px 6px;
    box-shadow: 0 3px 0 #222527;
    -webkit-transition: 0.1s;
    transition: 0.1s;
    font-size: 1.2rem;
    font-weight: 500;
}

.button button:hover {
    -webkit-transform: translateY(-3px);
            transform: translateY(-3px);
    box-shadow: 0 6px 0 #151f29;
}

.button button:active {
    -webkit-transform: translateY(1px);
            transform: translateY(1px);
    box-shadow: 0 3px 0 #151f29;
}

/* footer stuff */

.footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    color: #cacaca;
    font-weight: 600;
    display: flex;
    justify-content: space-between;
    background-color: #24282e;
}

/* Darkmode */

.darkmode.Live {
    background-color: #088a91;
}

.darkmode.Offline {
    background-color: #262b30;
}

.stream-container.darkmode {
    border-color: black;
}


.darkmode hr {
    border-color: black;
    color: black;
    background-color: black;
}

.darkmode {
    background-color: #354050;
}

.darkmode * {
    color: #eeeeee;
}

.darkmode .button button{
    background-color: #393e46;
    border-color: black;
}

.darkmode.title-hover {
    background: #262b30;
}

/* Litemode  */

.litemode.Offline {
    background-color: #65baff; 
}

.litemode.Live {
    background-color: #ff5252;
}

.litemode hr {
    border-color: #000000;
    color: #000000;
    background-color: #000000;
}

.stream-container.litemode {
    border-color: #000000;
}

.litemode .button button {
    background-color: #65baff;
    border-color: #000000;
}

.litemode {
    background-color: #84efff;
}

.litemode h1 {
    color: #000000;
}

.litemode.title-hover {
    background: #65baff;
}


/* responsive stuff */

@media (max-width: 1200px) {
    .people-container {
        width: 100%;
    }
}

@media (max-width: 600px) {
    .people-container {
        justify-content: center;
    }
}
.stream-container {
    border: 1px solid;
    border-radius: 10px;
    display: inline-block;
    width: 250px;
    height: 150px;
    margin: 5px;
    box-shadow: 0 5px 0 #222527;
    -webkit-transition: 0.15s;
    transition: 0.15s;
    margin: 3px auto;
}

.stream-container:hover {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
}

.stream-info-container {
    display: flex;
    justify-content: space-between;
    padding: 0 20%;
}

.stream-title {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding: 5px 7px;
    cursor: default;
}

.stream-title:hover + .title-hover {
    visibility: visible;
}

.title-hover {
    visibility: hidden;
    position: absolute;
    right: 50%;
    -webkit-transform: translate(50%, -100%);
            transform: translate(50%, -100%);
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    overflow: visible;
    height: auto;
    border: 1px solid black;
    border-radius: 5px;
    box-shadow: 0 3px 0 #151f29;
}

.title-hover:hover {
    visibility: visible;
}

.title-hover:active {
    visibility: visible;
}

/* Streamer link and info styling */

.icon {
    width: 25px;
    -webkit-transition: 0.2s;
    transition: 0.2s;
}

.icon:hover {
    -webkit-transform: scale(1.25);
            transform: scale(1.25);
}

.user {
    font-size: 1.2rem;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    margin: 5px auto;
    -webkit-transition: 0.2s;
    transition: 0.2s;
    text-decoration: none;
}

.user:hover {
    -webkit-transform: scale(1.15);
            transform: scale(1.15);
}
